.container {
  margin: 40px 40px;
  background-color: rgba(255, 255, 255, 0.651);
  padding: 10px 40px 40px 40px;
  border-radius: 24px;
  backdrop-filter: blur(20px);
}

.title {
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 36px;
  line-height: 42px;
  color: var(--black);
  padding: 20px 0;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
